import React from 'react';
import mData from './json/music_data.json';

const Music = () => {
  const fruits = ['', '', ''];
  return (
    <>
      <h3 className="title3">map json </h3>
      <h4 className="title4"></h4>
      <ul className="list01">
        <li></li>
        <li></li>
        <li></li>
      </ul>

      <h4 className="title4">map  </h4>
      <ul className='list01'>
        {fruits.map((fruit, index) => (
          <li key={index}>{fruit}</li>
        ))}
      </ul>

      <h4 className="title4">Music (json)</h4>
      {mData.slice(0, 4).map(music => (
        <ul className='list01' key={music.id}>
          <li>{music.id}</li>
          <li>{music.title}</li>
          <li>{music.url}</li>
          <li>{music.thumbnailUrl}</li>
        </ul>
      ))}
    </>
  );
};

export default Music;